import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { GuestLink } from '../models/guest-link.model';
import { URLS } from '../configuration';
import { map } from 'rxjs/operators';
import { DataUsage, FilesHistory } from '../models';

@Injectable({
  providedIn: 'root'
})
export class GuestLinksService {
  constructor(private httpClient: HttpClient) { }

  /**
   * Get Guest Link
   */
  getGuestLinks(): Observable<GuestLink[]> {
    return this.httpClient.get<GuestLink[]>(URLS.guestLink);
  }

  /**
   * Get Guest Link
   */
  generateGuestLinks(data: GuestLink): Observable<GuestLink> {
    return this.httpClient.post<GuestLink>(URLS.guestLink, data);
  }

   /**
   * Get Guest Link Usage
   */
  guestUsage(guestLinkId: number): Observable<DataUsage> {
    return this.httpClient.get<DataUsage>(`${URLS.guestLinkDataUsage}?GuestLinkId=${guestLinkId}`);
  }

    /**
   * Get Guest Link Usage
   */
  guestLinkAttachmentHistory(guestLinkId: number): Observable<FilesHistory[]> {
    return this.httpClient.get<FilesHistory[]>(`${URLS.guestLinkAttachmentHistory}${guestLinkId}`);
  }
}

