import { Component, OnInit, Optional, Inject } from '@angular/core';
import { GuestLinksService } from 'src/app/services/guest-links.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { GuestLink } from '../../../models/guest-link.model';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'am-guest-link',
  templateUrl: './guest-link.component.html',
  styleUrls: ['./guest-link.component.scss']
})
export class GuestLinkComponent implements OnInit {

  currentDate = new Date();
  guestLinkForm: FormGroup;

  constructor(private guestLinkService: GuestLinksService,
    private dialogRef: MatDialogRef<GuestLinkComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public guestLink: GuestLink) {
  }

  ngOnInit() {
    this.initalizeForm();

    const JSON_DATA: GuestLink = Object.assign(this.guestLink, this.guestLinkForm.value);

    this.guestLinkService.generateGuestLinks(JSON_DATA).subscribe(result => {
      if (result) {
        this.guestLink = result;
        this.guestLink.linkUrl = `${window.location.origin}/guestLink/${result.guestLinkID}`;
        this.guestLink.expirySettings.expirationDate = new Date(this.guestLink.expirySettings.expirationDate);
        this.guestLinkForm.reset(this.guestLink);
      }
    });
  }

  initalizeForm(): void {
    this.guestLinkForm = new FormGroup({
      expirySettings: new FormGroup({
        uploadLimit: new FormControl(this.guestLink.expirySettings.uploadLimit, Validators.required),
        numberOfUses: new FormControl(this.guestLink.expirySettings.numberOfUses, Validators.required),
        expirationDate: new FormControl(new Date(this.guestLink.expirySettings.expirationDate))
      }),
      notifySettings: new FormGroup({
        onDownloaded: new FormControl(this.guestLink.notifySettings.onDownloaded),
        onExpired: new FormControl(this.guestLink.notifySettings.onExpired),
        byEmail: new FormControl(this.guestLink.notifySettings.byEmail),
        byText: new FormControl(this.guestLink.notifySettings.byText)
      }),
      securitySettings: new FormGroup({
        email: new FormControl(this.guestLink.securitySettings.email),
        company: new FormControl(this.guestLink.securitySettings.company),
        name: new FormControl(this.guestLink.securitySettings.name),
        phone: new FormControl(this.guestLink.securitySettings.phone),
        password: new FormControl(this.guestLink.securitySettings.password),
        passwordProtectedPage: new FormControl(this.guestLink.securitySettings.passwordProtectedPage)
      })
    });
  }

  /**
   * Update Guest Name
   * @param name
   */
  updateGuestName(name: string): void {
    this.guestLink.linkName = name;
  }

  /**
   * Save guest Link
   * @param name
   */
  saveGuestLink(): void {
    this.callApiToUpdateGuestLink(this.guestLink, true);
  }

  saveGuestLinkName(name: string) {
    this.guestLink.linkName = name;
    this.callApiToUpdateGuestLink(this.guestLink, false);
  }

  callApiToUpdateGuestLink(link: GuestLink, closeDialog: boolean) {
    const JSON_DATA: GuestLink = Object.assign(link, this.guestLinkForm.value);
    this.guestLinkService.generateGuestLinks(JSON_DATA).subscribe(result => {
      if (result && closeDialog) {
        this.dialogRef.close(result);
      }
    });
  }
}
