import { Injectable, Inject } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { AttachmentService } from '../services/attachment.service';
import { FilesHistory } from '../models';
import { DashboardService } from '../services/dashboard.service';
import { GuestLinksService } from '../services/guest-links.service';
import { map } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class AttachmentHistoryResolver implements Resolve<FilesHistory[]> {
    constructor(private attachmentService: AttachmentService,
        private guestLinkService: GuestLinksService,
        private dashboardService: DashboardService) {
    }
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any {
        const guestLinkId = state.root.queryParams.guestLink;

        let fileHistory$: Observable<FilesHistory[]>;

        if (guestLinkId) {
            fileHistory$ = this.guestLinkService.guestLinkAttachmentHistory(+guestLinkId);
        }
        fileHistory$ = this.dashboardService.getFilesHistory();

        return fileHistory$.pipe(map((arr) => {
            return arr.map((value, index, array) => {
                const attachmentTypeArr = value.attachmentType.split('.');
                return {
                    ...value,
                    extension: (attachmentTypeArr && attachmentTypeArr.length) ? attachmentTypeArr.pop().toLowerCase() : 'zip',
                };
            });
        }));
    }
}
